
import React, { useState, useEffect, useRef } from 'react';
import classNames from 'classnames';


import { useHistory } from "react-router-dom";
import { Button } from "primereact/button";
import PrimeReact from 'primereact/api';

import 'primereact/resources/themes/saga-blue/theme.css';
import 'primereact/resources/primereact.min.css';
import 'primeicons/primeicons.css';
import 'primeflex/primeflex.css';
import 'prismjs/themes/prism-coy.css';
import '@fullcalendar/core/main.css';
import '@fullcalendar/daygrid/main.css';
import '@fullcalendar/timegrid/main.css';
import './layout/flags/flags.css';
import './layout/layout.scss';
import './AppLogin.css';
import { Checkbox } from 'primereact/checkbox';



const AppLogin = () => {
    const history = useHistory()
    const [layoutMode, setLayoutMode] = useState('static');
    const [layoutColorMode, setLayoutColorMode] = useState('dark')
    const [staticMenuInactive, setStaticMenuInactive] = useState(false);
    const [overlayMenuActive, setOverlayMenuActive] = useState(false);
    const [mobileMenuActive, setMobileMenuActive] = useState(false);
    const [inputStyle, setInputStyle] = useState('outlined');
    const [ripple, setRipple] = useState(false);
    const [showRegister, setShowRegister] = useState(false);
    const sidebar = useRef();
    let menuClick = false;

    const [newUser, setNewUser] = useState({ name: "", last_name: "", address: "", phone: "", email: "", password: "", role_id: 2 });
    useEffect(() => {
        addClass(document.body, 'body-overflow-hidden-login');


    }, [mobileMenuActive]);


    const handleClick = () => {
        history.push('/admin/gestion');

    }

    const onInputStyleChange = (inputStyle) => {
        setInputStyle(inputStyle);
    }

    const onRipple = (e) => {
        PrimeReact.ripple = e.value;
        setRipple(e.value)
    }

    const onLayoutModeChange = (mode) => {
        setLayoutMode(mode)
    }

    const onColorModeChange = (mode) => {
        setLayoutColorMode(mode)
    }

    const onWrapperClick = (event) => {
        if (!menuClick) {
            setOverlayMenuActive(false);
            setMobileMenuActive(false);
        }
        menuClick = false;
    }

    const onToggleMenu = (event) => {
        menuClick = true;

        if (isDesktop()) {
            if (layoutMode === 'overlay') {
                setOverlayMenuActive(prevState => !prevState);
            }
            else if (layoutMode === 'static') {
                setStaticMenuInactive(prevState => !prevState);
            }
        }
        else {
            setMobileMenuActive(prevState => !prevState);
        }
        event.preventDefault();
    }

    const onSidebarClick = () => {
        menuClick = true;
    }

    const onMenuItemClick = (event) => {
        if (!event.item.items) {
            setOverlayMenuActive(false);
            setMobileMenuActive(false);
        }
    }

    const addClass = (element, className) => {
        if (element.classList)
            element.classList.add(className);
        else
            element.className += ' ' + className;
    }

    const removeClass = (element, className) => {
        if (element.classList)
            element.classList.remove(className);
        else
            element.className = element.className.replace(new RegExp('(^|\\b)' + className.split(' ').join('|') + '(\\b|$)', 'gi'), ' ');
    }

    const isDesktop = () => {
        return window.innerWidth > 1024;
    }

    const isSidebarVisible = () => {
        if (isDesktop()) {
            if (layoutMode === 'static')
                return !staticMenuInactive;
            else if (layoutMode === 'overlay')
                return overlayMenuActive;
            else
                return true;
        }

        return true;
    }

    const logo = layoutColorMode === 'dark' ? 'assets/layout/images/logo-white.svg' : 'assets/layout/images/logo.svg';

    const wrapperClass = classNames('layout-wrapper', {
        'layout-overlay': layoutMode === 'overlay',
        'layout-static': layoutMode === 'static',
        'layout-static-sidebar-inactive': staticMenuInactive && layoutMode === 'static',
        'layout-overlay-sidebar-active': overlayMenuActive && layoutMode === 'overlay',
        'layout-mobile-sidebar-active': mobileMenuActive,
        'p-input-filled': inputStyle === 'filled',
        'p-ripple-disabled': ripple === false
    });

    const sidebarClassName = classNames('layout-sidebar', {
        'layout-sidebar-dark': layoutColorMode === 'dark',
        'layout-sidebar-light': layoutColorMode === 'light'
    });
    const onInputChange = (e, name) => {

        let _newUser = { ...newUser };
        _newUser[name] = (e.target && e.target.value) || '';
        //console.log('onInputChange',_newUser);
        setNewUser(_newUser);
    }
    return (
        <div className="container" id="container" style={{ minHeight:  500 }}>
            <div className="form-container sign-in-container">

                <div className="social-container" style={{ borderBottom: 10, borderBottomColor: '#9F191D', backgroundColor: '#B3AAA9', borderBottomStyle: 'solid', height: 104, }}>


                    <div className="p-d-flex">
                        <div className="p-mt-5 p-ml-5"><img alt="Logo" height='50' src='assets/layout/images/logo.png' /></div>
                        <div className="p-mt-5 p-ml-5">
                            <span style={{ color: 'white', fontSize: 18, fontFamily: '-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif' }}>Cobranzas Per√∫</span>
                            <br />
                            <span style={{ color: 'white', fontSize: 16, fontFamily: '-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif' }}>SISTEMA DE GESTI√ìN DE CARTERAS Y COBRANZAS</span>
                        </div>
                    </div>
                </div>


                <div className="p-grid" >


                    <>

                        <div className="p-col-10 p-pt-6 p-ml-6">
                            <div className="form">
                                <h5 >Iniciar Sesi√≥n</h5>
                                <div style={{ textAlign: 'left', width: '100%' }}>
                                    <label >Usuario:</label>
                                </div>
                                <input onChange={(e) => onInputChange(e, 'email')} type="email" placeholder="Email" />
                                <div style={{ textAlign: 'left', width: '100%' }}>
                                    <label>Contrase√±a:</label>
                                </div>
                                <div style={{ textAlign: 'left', width: '100%' }}>
                                    <input onChange={(e) => onInputChange(e, 'password')} type="password" placeholder="Password" />
                                </div>
                                <Button onClick={handleClick} label="Ingresar" icon="pi pi-check" className="p-button" />
                                <div className="social-container"></div>
                            </div>

                        </div>
                    </>


                </div>


            </div>
        </div>
    );
    //<AppFooter />

}

export default AppLogin;
