import axios from 'axios';
import moment from 'moment'
const api = axios.create({
    baseURL: process.env.REACT_APP_ROUTE_API,
})
const config = {
    headers: {
      'Content-Type': 'application/json',
      'accept':'application/json'
      
    },
  };

export class PeriodoService {
    getAll() {
        return api.get('periodos').then(res => res.data);
    }
    delete(id) {
        return api.delete('periodo/'+id).then(res => res.data);
    }
    save(entity) {

        //entity.fecVigenciaInicio = moment(new Date(entity.fecVigenciaInicio)).format("DD-MM-yy");
        //entity.fecVigenciaFin = moment(new Date(entity.fecVigenciaFin)).format("DD-MM-yy");
        return api.post('periodo', entity).then(res => res.data);
    }

    async saveAsync(entity) {
        //entity.fecVigenciaInicio = moment(new Date(entity.fecVigenciaInicio)).format("DD-MM-yy");
        //entity.fecVigenciaFin = moment(new Date(entity.fecVigenciaFin)).format("DD-MM-yy");        
        const res =  await api.post('periodo', entity).then(res => res.data);
        return await res.data;
    }

    update(entity) {
        //entity.fecVigenciaInicio = moment(new Date(entity.fecVigenciaInicio)).format("DD-MM-yy");
        //entity.fecVigenciaFin = moment(new Date(entity.fecVigenciaFin)).format("DD-MM-yy");
        return api.put(`periodo/${entity.codPeriodo}`, entity).then(res => res.data); 
    }

    async updateAsync(entity) {
        //entity.fecVigenciaInicio = moment(new Date(entity.fecVigenciaInicio)).format("DD-MM-yy");
        //entity.fecVigenciaFin = moment(new Date(entity.fecVigenciaFin)).format("DD-MM-yy");        
        const res =  await api.put(`periodo/${entity.codPeriodo}`, entity).then(res => res.data);
        return await res.data;
    }    
}