import React, { Component, useReducer } from 'react'
import { GET_BIODIVERSITIES ,GET_PROJECT,SET_ETIQUETAS } from '../../types';
import PanelContext from './PanelContext';
import PanelReducer from './PanelReducer';

const PanelState = (props) => {
    const initialState = {
        biodiversities: [],
        etiquetas: [],
        selectBiodiversity: null
    }
    const [state, dispatch] = useReducer(PanelReducer, initialState);

    const getBiosystems = () => { 

        dispatch({ type: GET_BIODIVERSITIES, payload: [{id: 1, name:"aa"},{id: 2, name:"vvv"},{id: 3, name:"ccc"}] });
    };
    const getProject = () => { 
        console.log('aaaaaaaaaaaaaaaaaa');
        dispatch({ type: GET_PROJECT, payload: [{name:"aa"}] });
    };
    const setEtiquetas = (e) => { 
       
        dispatch({ type: SET_ETIQUETAS, payload: e });
    };

    return(
        <PanelContext.Provider value={{
            biodiversities: state.biodiversities,
            selectBiodiversity: state.selectBiodiversity,
            etiquetas: state.etiquetas,
            getBiosystems,
            getProject,
            setEtiquetas
          }}>
            {props.children}
        </PanelContext.Provider>
    )
}
export default PanelState;  