import { GET_BIODIVERSITIES, GET_PROJECT, SET_ETIQUETAS } from "../../types";

// eslint-disable-next-line import/no-anonymous-default-export
export default (state, action) => {
  const { payload, type } = action;

  switch (type) {
    case GET_BIODIVERSITIES:
      return {
        ...state,
        biodiversities: payload,
      };
    case GET_PROJECT:
      return {
        ...state,
        selectBiodiversity: payload,
      };
    case SET_ETIQUETAS:
      return {
        ...state,
        etiquetas: payload,
      };
    default:
      return state;
  }
};
